.friends {
  background-color: #ffb297;
  padding: 1px 0;
  margin: 30px 0;
}

.title {
  text-align: center;
  color: #963a19;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: bold;
}

.list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.listItem {
  width: 33%;
  text-align: center;
  padding-bottom: 15px;
}

.icon {
  display: block;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
  margin: 0 auto;
}

.name {
  font-size: 14px;
  line-height: 1;
  color: #fff;
  text-decoration: none;
}