.wrapper {

}

.item {
  display: flex;
  background-color: #f6f6f6;
  border-radius: 5px;
  margin-bottom: 15px;
  padding: 15px;
}

.info {
  padding: 0 15px;
}

.iconBox {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.icon {
  display: block;
  width: 50px;
  height: auto;
  border-radius: 50%;
}

.fullname {
  font-size: 18px;
  color: #333;
  font-weight: bold;
  padding: 0;
  margin: 0;
}

.location {
  font-size: 14px;
  color: #666;
  padding: 0;
  margin: 0;
}

.status {
  font-size: 14px;
  color: #333;
}

.btn {
  width: 70px;
  font-size: 12px;
  color: #333;
  margin-top: 10px;
  cursor: pointer;
}
