.pageNavi {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding-bottom: 15px;
}

.pageNavi span {
  display: inline-block;
  font-size: 16px;
  color: #333;
  padding: 0 5px;
  border: 1px solid rgba(255, 255, 255, 0);
  cursor: pointer;
}

.pageNavi span.activePage {
  font-weight: bold;
  border: 1px solid #eee;
}

.prevBtn {
  margin-right: 10px;
}

.nextBtn {
  margin-left: 10px;
}
